body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    background-color: #111 ;

}



.logo{
    position: fixed;
    left: 20px;
    width: 80px;

}

.avatar{
    position: fixed;
    right: 20px;
    width: 30px;
}


*{
  margin: 0;
  padding: 0;
}

body{
  background-color:#111 ;
}
.banner{
    background-size: cover;
    height: 448px;
    color: white;
    cursor: pointer;
   
}

.content{
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;

}

.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}


.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51,51,51,5);
    cursor: pointer;
    margin-right: 1rem;
}

.button:hover{
    color: black;
    background-color:#e6e6e6 ;
}

.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;

}

body{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif ;
}

.fade_bottom{
    height:7.4rem ;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}
.banner:hover{
    -webkit-transform: scale();
            transform: scale();
}
.row{
    margin-left: 20px;
    color: #fff;
}

.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
    scroll-behavior: inherit;
    

}

.poster{
    max-height: 250px;
    margin-right: 10px;
    cursor: pointer;

}

.posters::-webkit-scrollbar{
    display: none;

}

.smallPosters{
    max-height: 150px;
    margin-right: 10px;
    cursor: pointer;
}

.poster:hover{
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
}
.smallPosters:hover{
    -webkit-transform: scale(1.11);
            transform: scale(1.11);
}
